/* App.css */
div {
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(255, 255, 255);
}

img {
  width: 100%;
  height: auto;
  box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.384);
  transition: transform 0.3s ease;
  cursor: pointer; /* Hozzáadva a kurzor változtatásához, jelezve, hogy a képre kattinthatunk */
}

img:hover {
  transform: translateY(-20px);
}

.new-images {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.new-images img {
  width: 100%; /* Megosztva a helyet a két új kép között */
  height: auto;
  box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.20);
}

@media (min-width: 600px) {
  /* A 600px széles vagy annál szélesebb kijelzőkre alkalmazódik */
  div {
    width: 720px;
  }
}